<script>
import firebase from "firebase";
let db = firebase.firestore()
import autophp from '@/plugins/autophp.js'
let sdb = new autophp()

export default {
  data() {
    return {
      vdata: {
        username: "",
        password: ""
      },
      cordova: false,
      error: null
    };
  },
  methods: {
    loginAuth() {
      let that =this;
      if(this.vdata.username=='admin' && this.vdata.password=='admin'){
        this.$router.push('/admin')
      }else{
            sdb.collection('tbuser').login2(this.vdata,this.$store,this.$router,'/').then(res=>{
              sdb.collection().ceklogin(that.$store).then(res=>{
                this.$router.push('/')
                      }).catch(err=>{
                        this.$router.push('/auth/login')
                      })
            }).catch(e=>{
              console.log(e)
              alert('username atau password salah')
            })
          }
    },
    register(){
      //  sdb.collection('tbuser').register(this.vdata).then(res=>{
      //           console.log(res)
      //       })
    },
   googleAuth() {
      const provider = new firebase.auth.GoogleAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(data => {

          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified,
            emailnotif:data.user.email,
            // bagian: 'unit_head',
            changepassword:"true",
            unit:{
              nama_unit:"BELUM ADA"
            }
          };
          firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
            localStorage.setItem("auth._token.local", 'bearer '+idToken);
            localStorage.setItem("auth.local", 'bearer '+idToken);
            }).catch(function(error) {
          });
          this.$store.commit("changeUsers", user);
          console.log('user',user)
          localStorage.setItem("users", JSON.stringify(user));
          db.collection("users")
            .doc(data.user.uid)
            .set(user, { merge: true }).then(res=>{
              this.Oauth=true
              this.$nuxt.$emit('busy',true)
              setTimeout(() => {
              this.$nuxt.$emit('busy',false)
                this.$router.push("/lembur");
              }, 3000);
            })
        })
        .catch(err => {
          alert("Oops. " + err.message);
        });
    },
    facebookAuth() {
      const provider = new firebase.auth.FacebookAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(data => {
          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified
          };
          this.$store.commit("changeUsers", user);
          localStorage.setItem("users", JSON.stringify(user));
           this.Oauth=true
          this.$router.push("/lembur");
        })
        .catch(err => {
          alert("Oops. " + err.message);
        });
    },
    cordovaGoogleAuth() {
      let that = this;
      window.plugins.googleplus.login(
        {
          webClientId:
            "494678481790-it9nrcbl4811c668mfpf6865tfqrovij.apps.googleusercontent.com",
          offline: true
        },
        function(obj) {
          firebase
            .auth()
            .signInWithCredential(
              firebase.auth.GoogleAuthProvider.credential(obj.idToken)
            )
            .then(data => {
              let user = {
                uid: data.user.uid,
                displayName: data.user.displayName,
                email: data.user.email,
                emailVerified: data.user.emailVerified,
             changepassword:"true",
            unit:{
              nama_unit:"BELUM ADA"
            }

              };
              firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
                localStorage.setItem("auth._token.local", 'bearer '+idToken);
                localStorage.setItem("auth.local", 'bearer '+idToken);
                }).catch(function(error) {
              });
              that.$store.commit("changeUsers", user);
              localStorage.setItem("users", JSON.stringify(user));
              //  alert(JSON.stringify(user))
              console.log(JSON.stringify(user))
              db.collection("users")
                .doc(data.user.uid)
                .set(user, { merge: true }).then(res=>{
                  this.Oauth=true
                          that.$router.push("/lembur");
                })
            })
            .catch(err => {
              alert("Oops. " + err.message);
            });
        },
        function(msg) {
          alert("error: " + msg);
        }
      );
    }
  },
  mounted() {
    // this.$store.dispatch("ceklogin", { true: "/driver", false: true });
    let that = this;
    document.addEventListener("deviceready", function() {
      that.cordova = true;
    });
